import React from 'react'
import {Modal , ModalHeader,ModalBody} from 'reactstrap'

function RenderModal(props){
    if(props != null){
    return(
    <Modal isOpen={props.isOpen} toggle={props.Login}>
         <ModalHeader toggle={props.Login}>{props.technologie.name}</ModalHeader>
         <ModalBody></ModalBody>
    </Modal>
    )
}
}
export default RenderModal;