import React, { useState } from 'react';
import {
    Collapse,
    Navbar,
    NavbarToggler,
    Nav,
    NavItem,
  } from 'reactstrap';
import {NavLink} from 'react-router-dom'
import './Header.css'


const Header = (props)=>{
    const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (
    <div id="top">
        <div>
            <h1 className="m-3 ml-5">My header</h1>
            <div className="line"></div>
        </div>
    <Navbar light expand="md">
      <NavbarToggler onClick={toggle} />
      <Collapse isOpen={isOpen} navbar>
        <Nav className="mr-auto ml-5" navbar>
           <NavItem className="mr-3">
             <NavLink  className="nav-link" to ='/home'><span className="fa fa-home fa-lg mr-2"></span>Home</NavLink>
            </NavItem> 
            <NavItem className="mr-3">
                <NavLink className ="nav-link" to ="/langages"><span className="fa fa-code fa-lg mr-2"></span> Technologies </NavLink>
            </NavItem>  
            <NavItem className="mr-3">
                <NavLink  className="nav-link" to ='/about'><span className="fa fa-grin-beam fa-lg mr-2"></span> About Me</NavLink>
            </NavItem>
        </Nav>
        
      </Collapse>
    </Navbar>
  </div>
  );
}
export default Header;