import React from 'react'
import {Breadcrumb , BreadcrumbItem} from 'reactstrap'
import {Link} from 'react-router-dom'

const About =()=>{
    return(
       <div className="container mt-3">
           <div className="row ">
              <Breadcrumb>
                <BreadcrumbItem ><Link to="/home">Home</Link></BreadcrumbItem>
                <BreadcrumbItem  active>About Me</BreadcrumbItem>
              </Breadcrumb>
            <div className="col-12 ">
                    <h1>About Me </h1>
              </div>  
              <hr />
            </div>
           <div className="row ">
               <div className="col-12  col-md-5 mt-5 mb-5 ">
                   <div className="senani">
                   <img  src="/images/senani.jpg" alt="SenaniBasma" width ="400px" height="400px" hidden-md></img>
                   </div>
                </div>
               <div className="col-12 col-md-5  mt-5 mb-5 text-center" id="aboutme">
                   <p>First and formost , i’m a software engineering student who’s in love with writing code , 
                       ever since my first ‘Hello,World ‘ program in Python i knew i’ll become a programming addict , 
                       being able to make the computer do whatever i wanted seemed like magic to me . 
                       My passion pushed me to study more languages and technologies , 
                       i moved from writing ‘Hello,World’ to creating websites , and now i just can’t get enough , 
                       Front End Web developement calls to all my passions , it incorporates both , creativity and problem solving . 
                       i started to learn about Back End Web development too, my goal now is to become a succesful Fullstack Developer.</p>
               </div>
           </div>
           
       </div>
        
    );
}
export default About;
