import React ,{Component} from 'react';
import {Card,CardHeader,CardBody,CardText,CardImg,Breadcrumb,BreadcrumbItem,
    Button,Modal,ModalBody,ModalHeader} from 'reactstrap';
import './langages.css'
import {Link} from 'react-router-dom'
class Langages extends Component {
    constructor(props){
        super(props);
        this.state = {
            isOpen : false ,
            tech : 0
        }
        this.handleLogin = this.handleLogin.bind(this);
        this.Close = this.Close.bind(this);

    }
Close(){
    this.setState((state)=>{
        return(
            {isOpen : !this.state.isOpen}
        )
    })
}
renderModal(props){
    if(this.state.tech.id === props){
    return(
          <Modal isOpen={this.state.isOpen} toggle={this.Close} >
                <ModalHeader toggle={this.Close} className="text-center">{this.state.tech.name}</ModalHeader>
               <ModalBody className="text-center">{this.state.tech.wholeDesc}</ModalBody>
               <a href={this.state.tech.website} className="mr-auto ml-auto mb-3" ><button type="button" class="btn btn-primary">
                Know even more and read the documentation
                </button></a>
          </Modal>
    )
}
}
handleLogin(props){
    this.setState((state)=>{
        return(
            {isOpen : !this.state.isOpen , tech: props}
        )
    })
}
   
 renderTech (props) {
        return(
            <React.Fragment>
              <Card className="card mr-2" key={props.id}>
                    <CardHeader className="text-center">{props.name}</CardHeader>
                 <CardBody >
                    <div >
                      <CardImg  width = '100px' src={props.image} alt={props.name} top/>
                    </div>
                    <div >
                      <CardText>{props.description}</CardText>
                    </div>
                </CardBody>
               {/*<Link to ={`/langages/${props.id}`}></Link> */}
               <Button  className="bg-primary text-black" onClick ={()=>this.handleLogin(props)}>Know more</Button>
             </Card>
        </React.Fragment>
        )
           
              
}

render() { 
    const technologies = this.props.technologies.map((tech)=> {
        return(
            <div className="col-8 col-md-5 col-lg-3 mt-1 mb-1 mr-auto ml-auto" key={tech.id}> 
             {this.renderTech(tech)}
             {this.renderModal(tech.id)}
             </div>
       )
    })
   
        return ( 
            <div className="container mt-5 mb-5 ">
            <div className="row ">
              <Breadcrumb>
                <BreadcrumbItem ><Link to="/home">Home</Link></BreadcrumbItem>
                <BreadcrumbItem  active>Technologies</BreadcrumbItem>
              </Breadcrumb>
              <div className="col-12">
                 <h3>Technologies</h3>
                 <hr />
              </div> 
            </div>
           <div className="row ">
            {technologies}
           </div>
          
        </div> 
     
        );
    }
}
 
export default Langages;

