import React , {Component} from 'react'
import Header from './Header.js'
import Home from './Home.js'
import Footer from './Footer.js'
import About from './AboutMe.js'
import  RenderModal from './TechModal.js'
import {Switch , Route , Redirect} from 'react-router-dom'
import Langages from './Languages.js'
import TECHNOLOGIES from './info/technologies.js'

class Main extends Component {
  constructor(props){
    super(props);
    this.state={
      technologies : TECHNOLOGIES,
      isOpen : false 
    }
  }
  handleLogin(){
    this.setState((state)=>{
      return(
        {isOpen : !this.state.isOpen }
      )
    })
  }
  
  
 render(){
  const TechnoModal = ({match}) =>{
      return(
          < RenderModal technologie ={this.state.technologies.find((tech)=> tech.id === parseInt(match.params.techId , 10))}
           isOpen ={this.state.isOpen} Login={this.handleLogin}/>
      )
}
   return(
    <React.Fragment>
      <Header />
      <Switch>
          <Route exact path ="/home" component= {() => <Home/>}/>
          <Route  exact path ="/langages" component ={() => <Langages technologies = {this.state.technologies}/>  }/>
          <Route path ="/langages/:techId" component= {TechnoModal}/>
          <Route path="/about" component={()=> <About/>}/>
          <Redirect path ="/home"  component= {() => <Home/>}/>
      </Switch>
      <Footer />
    </React.Fragment>
   )
 }
}

export default Main;
